.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.header {
  background: #3d8dd6;
}

.header__logo img {
  width: 191px;
  height: 46px;
}

.header__search {
  position: relative;
}

.header__search-input {
  height: 42px;
  border: 0;
  border-radius: 3px;
  display: block;
  font-size: 14px;
  outline: none;
  transition: all .2s;
  background-color: #fff;
}

.header__search-btn {
  position: absolute;
  right: 20px;
  outline: none;
  top: 50%;
  transform: translateY(-50%);
}

.header__search-btn i {
  color: #dbdbdb;
  font-size: 18px;
}

.info__item {
  position: relative;
}

.info__item::before {
  position: absolute;
  content: "";
  display: inline-block;
  top: 10px;
  left: 0;
  background-image: url(../img/content/sprite.png);
}

.info__item_stocks::before {
  background-position: 0 -45px;
  height: 16px;
  width: 18px;
}

.info__item_reviews::before {
  background-position: -18px -45px;
  height: 17px;
  width: 19px;
}

.info__title {
  display: inline-block;
  border-bottom: 1px solid transparent;
  font-size: 15px;
  line-height: 15px;
  color: #fff;
  transition: .1s;
  text-decoration: none;
}

.info__desc {
  display: block;
  font-size: 14px;
  line-height: 16px;
  margin-top: 5px;
  text-decoration: none;
  color: #bee0ff;
  text-overflow: ellipsis;
  overflow: hidden;
}

.main-nav {
  background: #5aa0e0;
}

.menu-item a .fa {
  margin-right: 7px;
  opacity: .5;
}

#menu-header-menu-bottom .menu-item a {
  padding-left: 0;
  font-size: 14px;
  display: inline-block;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  position: relative;
}

#menu-header-menu-bottom .menu-item .sub-menu {
  background: gold;
  border-radius: 3px;
  overflow: hidden;
  -webkit-box-shadow: 1px 2px 10px -4px #777;
  box-shadow: 1px 2px 10px -4px #777;
  border: 0;
}

.menu-item {
  padding: 0 10px;
}

#menu-header-menu-bottom .menu-item .sub-menu .menu-item a {
  position: relative;
  cursor: pointer;
  color: #3e3e3e;
  white-space: nowrap;
}

#menu-header-menu-bottom .menu-item:hover a {
  color: gold;
}

.dropdown-menu {
  padding: 0;
}

#menu-header-menu-bottom .menu-item .sub-menu .menu-item a {
  padding: 10px 25px;
}

#menu-header-menu-bottom .menu-item.menu-item-has-children > a:after {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  display: inline-block;
  padding-left: 10px;
  text-align: right;
  content: "\f107";
}

.dropdown-toggle::after {
  border: 0;
}

#menu-header-menu-bottom .menu-item .sub-menu .menu-item:hover a {
  color: #323232;
}

#menu-header-menu-bottom .menu-item .sub-menu .dropdown-item:hover {
  background: #ffe17b;
}

#menu-header-menu-bottom .menu-item .dropdown-menu:before {
  content: '';
  display: block;
  position: absolute;
  top: -6px;
  left: 12px;
  border-left: 6px solid transparent;
  border-bottom: 6px solid gold;
  border-right: 6px solid transparent;
}

@media only screen and (max-width: 1023px) {
  #menu-header-menu-bottom .menu-item .dropdown-menu:before {
    display: none;
  }
}

.navbar-nav .dropdown-menu {
  border: 0;
}

.dropdown-item {
  padding: 0;
  width: 100%;
}

#menu-header-menu-bottom .menu-item {
  padding: 0;
}

.nav-item {
  margin: 0px 15px;
}

#menu-header-menu-bottom .menu-item:hover {
  color: gold;
}

.navbar-toggler {
  position: relative;
}

.navbar-toggler-icon {
  background-image: url(../img/content/navbar.svg);
}

.slider__img {
  display: block;
}

.slider__link {
  width: 100%;
  display: block;
  overflow: hidden;
  transition: all .25s ease;
  position: relative;
}

.slider__link:hover .slider__link-overlay {
  background-color: rgba(0, 0, 0, 0.75);
}

.slider__link:hover .slider__rating {
  opacity: 1;
  top: 2px;
}

.slider__link:hover .slider__platform {
  opacity: 1;
  top: 4px;
}

.slider__link-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 0;
  left: 0;
  background: url(../img/content/bg/overlay_shadow.png) bottom left repeat-x;
  z-index: 1;
  transition: all .3s ease;
  background-color: rgba(0, 0, 0, 0);
}

.slider__top {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider__rating {
  position: relative;
  opacity: 0;
  top: -10px;
  transition: all .3s ease;
  display: inline-block;
  margin-top: 13px;
}

.slider__rating .rate {
  font-size: 16px;
  font-weight: 600;
  color: #ffcd02;
  text-shadow: 1px 1px 1px #555;
  margin-right: 5px;
}

.slider__platform {
  position: relative;
  top: -10px;
  opacity: 0;
  margin-top: 13px;
  transition: all .3s ease;
  display: block;
  text-align: center;
}

.slider__platform_uplay {
  display: inline-block;
  background: url(../img/content/sprite.png);
  background-position: 0 -136px;
  width: 44px;
  height: 18px;
}

.slider__bot {
  position: absolute;
  bottom: 10px;
  left: 2px;
  width: 100%;
  text-align: center;
  z-index: 2;
  margin-bottom: 25px;
}

.slider__bot-container {
  display: inline-block;
  position: relative;
}

.slider__bot-block {
  padding-left: 16px;
  padding-right: 16px;
  box-sizing: content-box;
  border-radius: 50px;
  border: 2px solid #808180;
  display: inline-block;
}

.slider__bot-block-price_old {
  display: inline-block;
  height: 36px;
  line-height: 35px;
  color: #808180;
  text-decoration: line-through;
}

.slider__bot-block-price_old_new {
  display: inline-block;
  font-size: 18px;
  height: 36px;
  line-height: 35px;
  color: #fff;
  margin-left: 10px;
}

.slider__bot-block-price_old_currency {
  display: inline-block;
  font-size: 18px;
  height: 36px;
  line-height: 35px;
  margin-left: 5px;
}

.main {
  padding: 10px;
}

.sidebar__slider {
  box-shadow: 0px 0px 15px #e4e4e4;
  width: 100%;
  height: 300px;
  max-height: 300px;
  overflow: hidden;
  background-color: #fff;
  font-size: 0px;
  line-height: 0;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  position: relative;
}

body {
  position: relative;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Roboto,sans-serif;
  color: #333;
}

input[type=text]::-ms-clear {
  display: none !important;
}
