.main-nav {
    background:#5aa0e0 {} }


.menu-item a .fa {
    margin-right: 7px;
    opacity: .5; }

#menu-header-menu-bottom .menu-item a {
    padding-left: 0;
    font-size: 14px;
    display: inline-block;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    outline: none;
    position: relative; }

#menu-header-menu-bottom .menu-item .sub-menu {
        background: gold;
        border-radius: 3px;
        overflow: hidden;
        -webkit-box-shadow: 1px 2px 10px -4px #777;
        box-shadow: 1px 2px 10px -4px #777;
        border: 0; }

.menu-item {
    padding: 0 10px; }


#menu-header-menu-bottom .menu-item .sub-menu .menu-item a {
    position: relative;
    cursor: pointer;
    color: #3e3e3e;
    white-space: nowrap; }
#menu-header-menu-bottom .menu-item {
    &:hover {
        a {
            color: gold; } } }
// #menu-header-menu-bottom .menu-item
//     &.show
//

.dropdown-menu {
    padding: 0; }
#menu-header-menu-bottom .menu-item .sub-menu .menu-item a {
    padding: 10px 25px; }

#menu-header-menu-bottom .menu-item.menu-item-has-children>a:after {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    display: inline-block;
    padding-left: 10px;
    text-align: right;
    content: "\f107"; }
.dropdown-toggle::after {
    border: 0; }

#menu-header-menu-bottom .menu-item .sub-menu .menu-item:hover a {
    color: #323232; }
#menu-header-menu-bottom .menu-item .sub-menu .dropdown-item:hover {
    background: #ffe17b; }


#menu-header-menu-bottom .menu-item .dropdown-menu:before {
    content: '';
    display: block;
    position: absolute;
    top: -6px;
    left: 12px;
    border-left: 6px solid transparent;
    border-bottom: 6px solid gold;
    border-right: 6px solid transparent;
    @include sm {
        display: none; } }


.navbar-nav .dropdown-menu {
    border: 0; }
.dropdown-item {
    padding: 0;
    width: 100%; }

#menu-header-menu-bottom .menu-item {
    padding: 0; }

.nav-item {
    margin: 0px 15px; }
#menu-header-menu-bottom .menu-item {
    &:hover {
        color: gold; } }


.navbar-toggler {
    position: relative; }
.navbar-toggler-icon {
    background-image: url(../img/content/navbar.svg); }

